// Multisynq Configuration
const MULTISYNQ_API_KEY = '2dNJ78OxelBvHA8vNqKR3r8Sf1W2RKELUYsuZo0whf';
const MULTISYNQ_USER_ID = '6876a905e273b89ce27dd663';
const APP_ID = 'com.chaosconductor.game';

// Sabitler
const TASK_DURATION = 15000;
const ROUND_COUNT = 10;
const MIN_PLAYERS = 2;
const MAX_PLAYERS = 8;

// GÃ¶rev tipleri
const TASK_TYPES = {
    CLICK: 'click',
    TYPE: 'type',
    DRAW: 'draw',
    FAKE: 'fake'
};

// Oyun durumu factory
function createGameState() {
    return {
        players: {},
        scores: {},
        currentTask: null,
        round: 0,
        gameStarted: false,
        gameEnded: false,
        roomId: null
    };
}

// Multisynq Model - Oyun durumunu yÃ¶netir
class ChaosGameModel extends Multisynq.Model {
    init(props) {
        super.init(props);
        this.gameState = createGameState();
        
        console.log('ChaosGameModel initialized:', this.sessionId);
        
        // Multisynq standard event handlers
        this.subscribe(this.sessionId, "view-join", this.onViewJoin);
        this.subscribe(this.sessionId, "view-exit", this.onViewExit);
        
        // Custom message handlers
        this.subscribe(this.sessionId, "taskComplete", this.handleTaskCompleted);
        this.subscribe(this.sessionId, "gameStateUpdate", this.handleGameStateUpdate);
        this.subscribe(this.sessionId, "newTask", this.handleNewTask);
        this.subscribe(this.sessionId, "sabotageUse", this.handleSabotageUsed);
        this.subscribe(this.sessionId, "chatMessage", this.handleChatMessage);
    }

    onViewJoin(viewId) {
        console.log(`Player ${viewId} trying to join...`);
        
        // VÃ©rifier si le serveur est plein
        if (Object.keys(this.gameState.players).length >= MAX_PLAYERS) {
            this.publish(viewId, "connection-refused", {
                reason: "Server is full",
                maxPlayers: MAX_PLAYERS,
                currentPlayers: Object.keys(this.gameState.players).length
            });
            console.log(`Player ${viewId} refused - server full`);
            return;
        }
        
        console.log(`Player ${viewId} joined the game!`);
        
        // S'abonner aux Ã©vÃ©nements du joueur
        this.subscribe(viewId, "set-name", "handleSetName");
        
        // CrÃ©er le joueur
        this.gameState.players[viewId] = {
            id: viewId,
            name: `Player${Object.keys(this.gameState.players).length + 1}`,
            ready: false,
            combo: 0
        };
        this.gameState.scores[viewId] = 0;
        
        const playerCount = Object.keys(this.gameState.players).length;
        console.log('Player joined:', viewId, 'Total players:', playerCount);
        
        // Broadcast player update
        this.publish(this.sessionId, "playersUpdate", {
            players: this.gameState.players,
            scores: this.gameState.scores,
            playerCount: playerCount
        });
    }

    onViewExit(viewId) {
        console.log(`Player ${viewId} left`);
        
        if (this.gameState.players[viewId]) {
            delete this.gameState.players[viewId];
            delete this.gameState.scores[viewId];
            
            const playerCount = Object.keys(this.gameState.players).length;
            
            // Broadcast player update
            this.publish(this.sessionId, "playersUpdate", {
                players: this.gameState.players,
                scores: this.gameState.scores,
                playerCount: playerCount
            });
            
            console.log('Player left, remaining players:', playerCount);
        }
    }

    handleSetName(data) {
        const { viewId, name } = data;
        if (this.gameState.players[viewId]) {
            this.gameState.players[viewId].name = name.substring(0, 15);
            
            // Broadcast player update
            this.publish(this.sessionId, "playersUpdate", {
                players: this.gameState.players,
                scores: this.gameState.scores,
                playerCount: Object.keys(this.gameState.players).length
            });
        }
    }


    handleTaskCompleted(data) {
        const { playerId, taskId, timeLeft, success } = data;
        
        if (this.gameState.currentTask && this.gameState.currentTask.id === taskId) {
            const player = this.gameState.players[playerId];
            if (player && success) {
                let points = this.gameState.currentTask.points;
                
                // HÄ±z bonusu
                const speedBonus = Math.floor(timeLeft / 1000) * 10;
                points += speedBonus;
                
                // Combo bonusu
                player.combo = (player.combo || 0) + 1;
                if (player.combo >= 3) {
                    points *= 1.5;
                    player.combo = 0;
                    this.publish("powerUp", { playerId, type: "combo" });
                }
                
                const currentScore = this.gameState.scores[playerId] || 0;
                this.gameState.scores[playerId] = currentScore + points;
                
                this.publish("scoreUpdate", {
                    playerId,
                    points,
                    speedBonus,
                    totalScore: currentScore + points
                });
            }
        }
    }

    handleGameStateUpdate(data) {
        Object.assign(this.gameState, data);
        this.publish("gameStateChanged", this.gameState);
    }

    handleNewTask(data) {
        this.gameState.currentTask = data.task;
        this.gameState.round = data.round;
        this.publish("taskUpdate", data);
    }

    handleSabotageUsed(data) {
        this.publish("sabotageActivated", data);
    }

    handleChatMessage(data) {
        this.publish("chatUpdate", data);
    }
}

// Multisynq View - UI gÃ¼ncellemelerini yÃ¶netir
// Multisynq View - UI gÃ¼ncellemelerini yÃ¶netir
class ChaosGameView extends Multisynq.View {
    init(model) {
        super.init(model);
        this.model = model; // Model referansÄ±nÄ± sakla
        this.gameState = createGameState();
        this.currentPlayerId = this.viewId;
        this.playerName = '';
        this.isFrozen = false;
        
        console.log('ChaosGameView initialized:', this.sessionId, this.viewId);
        
        // Connection events
        this.subscribe(this.viewId, "connection-refused", (data) => this.onConnectionRefused(data));
        
        // Event subscriptions - arrow function ile
        this.subscribe(this.sessionId, "playersUpdate", (data) => this.updatePlayers(data));
        this.subscribe(this.sessionId, "gameStateChanged", (data) => this.updateGameState(data));
        this.subscribe(this.sessionId, "taskUpdate", (data) => this.updateTask(data));
        this.subscribe(this.sessionId, "scoreUpdate", (data) => this.updateScore(data));
        this.subscribe(this.sessionId, "powerUp", (data) => this.showPowerUp(data));
        this.subscribe(this.sessionId, "sabotageActivated", (data) => this.handleSabotage(data));
        this.subscribe(this.sessionId, "chatUpdate", (data) => this.updateChat(data));
        
        // Periyodik olarak Model'den veri Ã§ek
        this.startModelSync();
    }

    startModelSync() {
        setInterval(() => {
            if (this.model && this.model.gameState) {
                // Model'den direkt veri al
                const modelPlayers = this.model.gameState.players || {};
                const modelScores = this.model.gameState.scores || {};
                const playerCount = Object.keys(modelPlayers).length;
                
                // EÄŸer Model'de oyuncu varsa ama View'da yoksa, gÃ¼ncelle
                if (playerCount > 0 && Object.keys(this.gameState.players || {}).length === 0) {
                    console.log('Syncing from Model - Players:', playerCount);
                    this.gameState.players = modelPlayers;
                    this.gameState.scores = modelScores;
                    this.renderPlayers();
                    this.renderScores();
                }
            }
        }, 1000);
    }
    onConnectionRefused({ reason, maxPlayers, currentPlayers }) {
        console.log('Connection refused:', reason);
        this.showNotification(`Sunucu dolu! (${currentPlayers}/${maxPlayers})`, 'error');
    }

    updatePlayers(data) {
        console.log('View updatePlayers called:', data);
        
        if (!data || !data.players) {
            console.warn('updatePlayers called with invalid data:', data);
            return;
        }
        
        // Ensure gameState exists
        if (!this.gameState) {
            this.gameState = createGameState();
        }
        
        this.gameState.players = data.players;
        this.gameState.scores = data.scores || {};
        
        const playerCount = Object.keys(this.gameState.players).length;
        console.log('Updating UI with player count:', playerCount);
        
        this.renderPlayers();
        this.renderScores();
    }

    updateGameState(gameState) {
        Object.assign(this.gameState, gameState);
        this.renderGameState();
    }

    updateTask(data) {
        this.gameState.currentTask = data.task;
        this.gameState.round = data.round;
        this.renderTask();
        this.startTaskTimer();
    }

    updateScore(data) {
        if (data.playerId === this.currentPlayerId) {
            this.showScoreNotification(data.points, data.speedBonus);
        }
        this.gameState.scores[data.playerId] = data.totalScore;
        this.renderScores();
    }

    showPowerUp(data) {
        if (data.playerId === this.currentPlayerId) {
            this.showNotification('ðŸ”¥ COMBO BONUS! ðŸ”¥', 'success');
        }
    }

    handleSabotage(data) {
        if (data.targetId === this.currentPlayerId) {
            this.freezePlayer(data.duration);
        }
        this.showNotification(`${data.playerName} sabotaj kullandÄ±!`, 'warning');
    }

    updateChat(data) {
        this.addChatMessage(data);
    }

    renderPlayers() {
        const playersList = document.getElementById('players-list');
        if (!playersList) return;

        if (!this.gameState || !this.gameState.players) {
            console.warn('gameState or players is undefined');
            this.gameState = this.gameState || createGameState();
            this.gameState.players = this.gameState.players || {};
        }

        playersList.innerHTML = '';
        const playerCount = Object.keys(this.gameState.players).length;
        
        Object.entries(this.gameState.players).forEach(([playerId, player]) => {
            if (!player) return;
            
            const playerCard = document.createElement('div');
            playerCard.className = 'player-card';
            if (player.ready) playerCard.classList.add('ready');
            
            playerCard.innerHTML = `
                <div class="player-name">${player.name || 'Unknown'}</div>
                <div class="player-status">${player.ready ? 'HazÄ±r' : 'Bekliyor'}</div>
            `;
            
            playersList.appendChild(playerCard);
        });

        // Oyuncu sayÄ±sÄ±nÄ± gÃ¼ncelle
        const playerCountElement = document.getElementById('player-count');
        if (playerCountElement) {
            playerCountElement.textContent = playerCount;
        }
        
        // Oyun baÅŸlatma butonunu kontrol et
        const startGameBtn = document.getElementById('start-game-btn');
        if (startGameBtn) {
            if (playerCount >= 2) {
                startGameBtn.disabled = false;
                startGameBtn.textContent = 'OYUNU BAÅžLAT';
                startGameBtn.style.opacity = '1';
            } else {
                startGameBtn.disabled = true;
                startGameBtn.textContent = `OYUNU BAÅžLAT (${playerCount}/2)`;
                startGameBtn.style.opacity = '0.5';
            }
        }
        
        console.log('Players rendered:', playerCount, 'players');
    }

    renderScores() {
        const scoresList = document.getElementById('scores-list');
        if (!scoresList) return;

        const sortedScores = Object.entries(this.gameState.scores)
            .sort((a, b) => b[1] - a[1]);

        scoresList.innerHTML = '';
        sortedScores.forEach(([playerId, score], index) => {
            const player = this.gameState.players[playerId];
            if (!player) return;

            const scoreItem = document.createElement('div');
            scoreItem.className = 'score-item';
            if (playerId === this.currentPlayerId) {
                scoreItem.classList.add('current-player');
            }

            scoreItem.innerHTML = `
                <span>${index + 1}. ${player.name}</span>
                <span>${score}</span>
            `;

            scoresList.appendChild(scoreItem);
        });
    }

    renderTask() {
        const taskDescription = document.getElementById('task-description');
        const currentRound = document.getElementById('current-round');

        if (taskDescription && this.gameState.currentTask) {
            taskDescription.textContent = this.gameState.currentTask.description;
            
            if (this.gameState.currentTask.isFake) {
                taskDescription.style.color = '#ff6b6b';
                taskDescription.style.textDecoration = 'line-through';
            } else {
                taskDescription.style.color = '';
                taskDescription.style.textDecoration = '';
            }
        }

        if (currentRound) {
            currentRound.textContent = this.gameState.round;
        }

        this.renderTaskElements();
    }

    renderTaskElements() {
        const gameUI = document.getElementById('game-ui');
        const canvas = document.getElementById('game-canvas');
        
        if (!gameUI || !canvas || !this.gameState.currentTask) return;

        gameUI.innerHTML = '';
        const task = this.gameState.currentTask;
        
        switch (task.type) {
            case 'click':
                this.createClickElements(gameUI, canvas, task);
                break;
            case 'type':
                this.createTypeElements(gameUI, task);
                break;
            case 'draw':
                this.createDrawElements(canvas, task);
                break;
        }
    }

    createClickElements(gameUI, canvas, task) {
        const rect = canvas.getBoundingClientRect();
        const colors = ['red', 'blue', 'green', 'yellow', 'purple', 'orange'];
        const shapes = ['circle', 'square', 'triangle'];
        
        const targetColor = task.targetColor || colors[Math.floor(Math.random() * colors.length)];
        const targetShape = task.targetShape || shapes[Math.floor(Math.random() * shapes.length)];
        
        const elementCount = 5 + Math.floor(Math.random() * 6);
        
        for (let i = 0; i < elementCount; i++) {
            const element = document.createElement('div');
            element.className = `game-element shape ${shapes[Math.floor(Math.random() * shapes.length)]}`;
            
            const color = i === 0 ? targetColor : colors[Math.floor(Math.random() * colors.length)];
            const shape = i === 0 ? targetShape : shapes[Math.floor(Math.random() * shapes.length)];
            
            element.style.backgroundColor = color;
            element.style.width = '60px';
            element.style.height = '60px';
            element.style.left = Math.random() * (rect.width - 60) + 'px';
            element.style.top = Math.random() * (rect.height - 60) + 'px';
            
            element.addEventListener('click', () => {
                if (color === targetColor && shape === targetShape && !this.isFrozen) {
                    this.completeTask(true);
                } else {
                    this.completeTask(false);
                }
            });
            
            gameUI.appendChild(element);
        }
    }

    createTypeElements(gameUI, task) {
        const typeContainer = document.createElement('div');
        typeContainer.style.position = 'absolute';
        typeContainer.style.top = '50%';
        typeContainer.style.left = '50%';
        typeContainer.style.transform = 'translate(-50%, -50%)';
        typeContainer.style.textAlign = 'center';
        
        const input = document.createElement('input');
        input.type = 'text';
        input.placeholder = `"${task.targetWord}" yazÄ±n...`;
        input.style.padding = '15px';
        input.style.fontSize = '1.2rem';
        input.style.borderRadius = '10px';
        input.style.border = 'none';
        input.style.textAlign = 'center';
        input.style.width = '300px';
        
        let typedCount = 0;
        const targetCount = task.targetCount || 3;
        
        input.addEventListener('keypress', (e) => {
            if (e.key === 'Enter' && !this.isFrozen) {
                if (input.value.toLowerCase() === task.targetWord.toLowerCase()) {
                    typedCount++;
                    input.value = '';
                    
                    if (typedCount >= targetCount) {
                        this.completeTask(true);
                    } else {
                        input.placeholder = `"${task.targetWord}" yazÄ±n... (${typedCount}/${targetCount})`;
                    }
                }
            }
        });
        
        typeContainer.appendChild(input);
        gameUI.appendChild(typeContainer);
        
        setTimeout(() => input.focus(), 100);
    }

    createDrawElements(canvas, task) {
        const ctx = canvas.getContext('2d');
        let isDrawing = false;
        let hasDrawn = false;
        
        ctx.strokeStyle = '#fff';
        ctx.lineWidth = 3;
        ctx.lineCap = 'round';
        
        const startDrawing = (e) => {
            if (this.isFrozen) return;
            isDrawing = true;
            hasDrawn = true;
            const rect = canvas.getBoundingClientRect();
            ctx.beginPath();
            ctx.moveTo(e.clientX - rect.left, e.clientY - rect.top);
        };
        
        const draw = (e) => {
            if (!isDrawing || this.isFrozen) return;
            const rect = canvas.getBoundingClientRect();
            ctx.lineTo(e.clientX - rect.left, e.clientY - rect.top);
            ctx.stroke();
        };
        
        const stopDrawing = () => {
            isDrawing = false;
            if (hasDrawn) {
                setTimeout(() => this.completeTask(true), 500);
            }
        };
        
        canvas.addEventListener('mousedown', startDrawing);
        canvas.addEventListener('mousemove', draw);
        canvas.addEventListener('mouseup', stopDrawing);
        canvas.addEventListener('mouseout', stopDrawing);
    }

    startTaskTimer() {
        const timer = document.getElementById('task-timer');
        const progressBar = document.querySelector('.progress-bar') || this.createProgressBar();
        
        if (!timer || !this.gameState.currentTask) return;
        
        let timeLeft = this.gameState.currentTask.duration / 1000;
        const totalTime = timeLeft;
        
        const updateTimer = () => {
            timer.textContent = Math.ceil(timeLeft);
            
            const progress = (timeLeft / totalTime) * 100;
            progressBar.style.width = progress + '%';
            
            if (timeLeft <= 0) {
                this.completeTask(false);
                return;
            }
            
            timeLeft -= 0.1;
            setTimeout(updateTimer, 100);
        };
        
        updateTimer();
    }

    createProgressBar() {
        const progressContainer = document.getElementById('task-progress');
        if (!progressContainer) return null;
        
        const progressBar = document.createElement('div');
        progressBar.className = 'progress-bar';
        progressBar.style.width = '100%';
        
        progressContainer.appendChild(progressBar);
        return progressBar;
    }

    completeTask(success) {
        if (!this.gameState.currentTask) return;
        
        const timer = document.getElementById('task-timer');
        const timeLeft = timer ? parseInt(timer.textContent) * 1000 : 0;
        
        // Model'e message gÃ¶nder
        this.publish("taskComplete", {
            playerId: this.currentPlayerId,
            taskId: this.gameState.currentTask.id,
            timeLeft,
            success
        });
        
        if (success) {
            this.showNotification('GÃ¶rev TamamlandÄ±! âœ…', 'success');
            this.playSound('task-complete');
        } else {
            this.showNotification('GÃ¶rev BaÅŸarÄ±sÄ±z! âŒ', 'error');
            this.playSound('task-fail');
        }
    }

    freezePlayer(duration) {
        this.isFrozen = true;
        document.body.classList.add('frozen');
        
        const overlay = document.createElement('div');
        overlay.className = 'sabotage-overlay';
        document.body.appendChild(overlay);
        
        this.showNotification('ðŸ§Š DONDURULDUN! ðŸ§Š', 'warning');
        this.playSound('sabotage');
        
        setTimeout(() => {
            this.isFrozen = false;
            document.body.classList.remove('frozen');
            overlay.remove();
        }, duration);
    }

    showScoreNotification(points, speedBonus) {
        let message = `+${points} puan!`;
        if (speedBonus > 0) {
            message += ` (HÄ±z bonusu: +${speedBonus})`;
        }
        this.showNotification(message, 'success');
    }

    showNotification(message, type = 'info') {
        const notifications = document.getElementById('notifications');
        if (!notifications) return;
        
        const notification = document.createElement('div');
        notification.className = `notification ${type}`;
        notification.textContent = message;
        
        notifications.appendChild(notification);
        
        setTimeout(() => {
            notification.remove();
        }, 3000);
    }

    addChatMessage(data) {
        const chatMessages = document.getElementById('chat-messages');
        if (!chatMessages) {
            console.warn('Chat messages container not found');
            return;
        }
        
        console.log('Adding chat message:', data);
        
        const message = document.createElement('div');
        message.className = 'chat-message';
        message.innerHTML = `
            <span class="sender">${data.playerName}:</span>
            ${data.message}
        `;
        
        chatMessages.appendChild(message);
        chatMessages.scrollTop = chatMessages.scrollHeight;
        
        const chatContainer = document.querySelector('.chat-container');
        if (chatContainer) {
            chatContainer.style.display = 'flex';
        }
    }

    playSound(soundId) {
        const audio = document.getElementById(soundId + '-sound');
        if (audio) {
            audio.currentTime = 0;
            audio.play().catch(e => console.log('Ses Ã§alÄ±namadÄ±:', e));
        }
    }
}

// Model sÄ±nÄ±fÄ±nÄ± register et
ChaosGameModel.register('ChaosGameModel');

// Ana Oyun SÄ±nÄ±fÄ±
class ChaosGame {
    constructor() {
        this.session = null;
        this.view = null;
        this.model = null;
        this.currentScreen = 'main-menu';
        this.playerName = '';
        this.roomCode = '';
        
        this.initializeUI();
        this.loadLiveGames();
    }

    initializeUI() {
        // Ana menÃ¼ event listeners
        document.getElementById('join-game-btn').addEventListener('click', () => {
            this.joinGame();
        });

        document.getElementById('create-room-btn').addEventListener('click', () => {
            this.createRoom();
        });

        document.getElementById('refresh-rooms-btn').addEventListener('click', () => {
            this.loadLiveGames();
        });

        // Lobby event listeners
        document.getElementById('start-game-btn').addEventListener('click', () => {
            this.startGame();
        });

        document.getElementById('leave-room-btn').addEventListener('click', () => {
            this.leaveRoom();
        });

        // Chat
        document.getElementById('send-chat-btn').addEventListener('click', () => {
            this.sendChatMessage();
        });

        document.getElementById('chat-input').addEventListener('keypress', (e) => {
            if (e.key === 'Enter') {
                this.sendChatMessage();
            }
        });

        // Oyun sonu
        document.getElementById('play-again-btn').addEventListener('click', () => {
            this.playAgain();
        });

        document.getElementById('main-menu-btn').addEventListener('click', () => {
            this.goToMainMenu();
        });
    }

    async joinGame() {
        this.playerName = document.getElementById('player-name').value.trim();
        this.roomCode = document.getElementById('room-code').value.trim();

        if (!this.playerName) {
            this.showNotification('LÃ¼tfen oyuncu adÄ±nÄ±zÄ± girin!', 'error');
            return;
        }

        if (!this.roomCode) {
            this.roomCode = 'default-room';
        }

        try {
            // Multisynq Session oluÅŸtur
            const result = await Multisynq.Session.join({
                apiKey: MULTISYNQ_API_KEY,
                appId: APP_ID,
                name: this.roomCode,
                model: ChaosGameModel,
                view: ChaosGameView,
                password: 'chaos2025'
            });

            this.session = result;
            this.model = result.model;
            this.view = result.view;

            // Oyuncu adÄ±nÄ± ayarla
            this.view.playerName = this.playerName;
            this.view.publish(this.view.viewId, "set-name", {
                viewId: this.view.viewId,
                name: this.playerName
            });

            this.switchScreen('lobby');
            this.updateRoomInfo();
            
            // Force initial render after joining
            setTimeout(() => {
                this.view.renderPlayers();
                this.forceUpdatePlayerCount();
            }, 500);
            
            // Periyodik olarak oyuncu sayÄ±sÄ±nÄ± gÃ¼ncelle
            this.playerCountInterval = setInterval(() => {
                this.forceUpdatePlayerCount();
            }, 1000);

        } catch (error) {
            console.error('Oyuna katÄ±lÄ±m hatasÄ±:', error.message || error);
            this.showNotification('Oyuna katÄ±lÄ±rken hata oluÅŸtu!', 'error');
        }
    }

    async createRoom() {
        this.roomCode = this.generateRoomCode();
        document.getElementById('room-code').value = this.roomCode;
        await this.joinGame();
    }

    generateRoomCode() {
        return Math.random().toString(36).substring(2, 8).toUpperCase();
    }

    startGame() {
        if (this.view && Object.keys(this.view.gameState.players).length >= 2) {
            this.view.publish("gameStateUpdate", {
                gameMode: 'playing',
                gameActive: true
            });
            
            this.switchScreen('game-screen');
            this.startGameLoop();
        }
    }

    startGameLoop() {
        setTimeout(() => {
            this.generateNewTask();
        }, 2000);
    }

    generateNewTask() {
        const taskTypes = [
            {
                type: 'click',
                description: 'KÄ±rmÄ±zÄ± daireye tÄ±kla!',
                duration: 10000,
                points: 100,
                targetColor: 'red',
                targetShape: 'circle'
            },
            {
                type: 'type',
                description: 'CHAOS yazÄ±n 3 kez!',
                duration: 15000,
                points: 150,
                targetWord: 'CHAOS',
                targetCount: 3
            },
            {
                type: 'draw',
                description: '5 saniyede bir ev Ã§izin!',
                duration: 8000,
                points: 200,
                targetObject: 'house'
            },
            {
                type: 'click',
                description: 'Bu gÃ¶rev sahte, gÃ¶rmezden gelin!',
                duration: 8000,
                points: 250,
                isFake: true
            }
        ];

        const taskTemplate = taskTypes[Math.floor(Math.random() * taskTypes.length)];
        const task = {
            ...taskTemplate,
            id: Date.now(),
            round: (this.view.gameState.round || 0) + 1
        };

        this.view.publish("newTask", {
            task,
            round: task.round
        });

        setTimeout(() => {
            this.generateNewTask();
        }, task.duration + 2000);
    }

    leaveRoom() {
        if (this.view) {
            this.view.publish("playerLeave", {
                playerId: this.view.viewId
            });
        }
        this.switchScreen('main-menu');
        this.resetGame();
    }

    sendChatMessage() {
        const input = document.getElementById('chat-input');
        const message = input.value.trim();
        
        if (message && this.view) {
            this.view.publish("chatMessage", {
                playerId: this.view.viewId,
                playerName: this.playerName,
                message,
                timestamp: Date.now()
            });
            
            input.value = '';
        }
    }

    playAgain() {
        this.resetGame();
        this.switchScreen('lobby');
    }

    goToMainMenu() {
        this.leaveRoom();
    }

    resetGame() {
        if (this.view) {
            this.view.gameState = createGameState();
        }
    }

    updateRoomInfo() {
        document.getElementById('room-name').textContent = this.roomCode;
        document.getElementById('current-room-code').textContent = this.roomCode;
    }

    switchScreen(screenId) {
        document.querySelectorAll('.screen').forEach(screen => {
            screen.classList.remove('active');
        });
        
        document.getElementById(screenId).classList.add('active');
        this.currentScreen = screenId;
    }

    showNotification(message, type = 'info') {
        const notifications = document.getElementById('notifications');
        if (!notifications) return;
        
        const notification = document.createElement('div');
        notification.className = `notification ${type}`;
        notification.textContent = message;
        
        notifications.appendChild(notification);
        
        setTimeout(() => {
            notification.remove();
        }, 3000);
    }

    loadLiveGames() {
        const liveGamesList = document.getElementById('live-games-list');
        if (!liveGamesList) return;

        liveGamesList.innerHTML = '<div class="loading">Aktif oyunlar yÃ¼kleniyor...</div>';
        
        // Multisynq'den aktif odalarÄ± Ã§ek
        // Bu kÄ±sÄ±m Multisynq API'sine gÃ¶re gÃ¼ncellenecek
    }

    forceUpdatePlayerCount() {
        if (this.view && this.view.gameState) {
            if (!this.view.gameState.players) {
                this.view.gameState.players = {};
            }
            
            const playerCount = Object.keys(this.view.gameState.players).length;
            const playerCountElement = document.getElementById('player-count');
            if (playerCountElement) {
                playerCountElement.textContent = playerCount;
                console.log('Forced player count update:', playerCount);
            }
            
            const startGameBtn = document.getElementById('start-game-btn');
            if (startGameBtn) {
                if (playerCount >= 2) {
                    startGameBtn.disabled = false;
                    startGameBtn.textContent = 'OYUNU BAÅžLAT';
                    startGameBtn.style.opacity = '1';
                } else {
                    startGameBtn.disabled = true;
                    startGameBtn.textContent = `OYUNU BAÅžLAT (${playerCount}/2)`;
                    startGameBtn.style.opacity = '0.5';
                }
            }
            
            this.view.renderPlayers();
        }
    }
}

